import React, { useRef } from "react";
import { Canvas, useFrame } from "@react-three/fiber";
import { useGLTF, OrbitControls } from "@react-three/drei";

function Lion({ ...props }) {
    const group = useRef();
    const { nodes, materials } = useGLTF("/Assets/gltf/lion.glb");

    useFrame(({ clock }) => {
        const rotationTime = clock.getElapsedTime();
        group.current.rotation.y = -rotationTime / 3;
    });

    return (
        <group ref={group} {...props} dispose={null} scale={1.5}>
            <directionalLight
                intensity={2.62}
                decay={2}
                color="#768bb2"
                position={[-0.4, 0.78, 2.37]}
                rotation={[-0.56, -0.23, -0.26]}
            />
            <mesh
                geometry={nodes.수염.geometry}
                material={nodes.수염.material}
                position={[0, -0.14, 0]}
            />
            <mesh
                geometry={nodes.코.geometry}
                material={nodes.코.material}
                position={[0, -0.09, 0.59]}
                scale={0.06}
            />
            <mesh
                geometry={nodes.눈썹.geometry}
                material={nodes.눈썹.material}
            />
            <mesh
                geometry={nodes.눈.geometry}
                material={nodes.눈.material}
                position={[0, -0.14, 0.07]}
            />
            <mesh
                geometry={nodes.Sphere.geometry}
                material={nodes.Sphere.material}
                position={[0, -0.76, 0]}
                scale={1.06}
            />
            <mesh
                geometry={nodes.후드.geometry}
                material={nodes.후드.material}
                rotation={[-0.01, 0, 0]}
                scale={1.26}
            />
            <mesh
                geometry={nodes.양팔.geometry}
                material={nodes.양팔.material}
            />
            <mesh
                geometry={nodes.Cylinder.geometry}
                material={nodes.Cylinder.material}
            />
            <mesh
                geometry={nodes.머리_.geometry}
                material={nodes.머리_.material}
                scale={1.19}
            />
        </group>
    );
}

export default Lion;
